
 /**
  * Grid 
  */
.page-title{
    grid-column-start: 4;
    grid-column-end: 12;
    grid-row:1;
}
.woocommerce-ordering{
    grid-column-start:4;
    grid-column-end:7;
    grid-row: 2;
}
.woocommerce-result-count{
    grid-column-start:7;
    grid-column-end:12;
}
.columns-3{
    grid-column-start:4;
    grid-column-end:13;
    display:inline-grid;
    grid-template-columns:1fr 1fr 1fr;
    grid-gap:2rem;
    grid-row-gap: 2rem; 

    li.product{
        a.woocommerce-loop-product__link{
            display:block;
            //background-color:$white;
            
            img{
                height:22rem;
            }
            h2.woocommerce-loop-product__title{
                
            }
        }
        a.button{

        }
          
    }
}


